import { BigNumber } from 'ethers';

export interface IItemDetails {
  balance: number;
  id: number;
  price: BigNumber;
  maxSupply: BigNumber;
  name: string;
  imageUrl: string;
  animationUrl: string;
  paymentContract: string;
  mintingActive: boolean;
  itemType: number;
  rarity: number;
}

export class ItemDetails {
  balance: number = 0;
  id?: number;
  price?: BigNumber;
  maxSupply?: BigNumber;
  name?: string;
  imageUrl?: string;
  animationUrl?: string;
  paymentContract?: string;
  mintingActive?: boolean;
  itemType?: number;
  rarity?: number;
}

export async function toItemDetails(details: any): Promise<IItemDetails> {
  const imageUrl = details['imageUrl'];
  const itemId = +imageUrl.split('/').pop().split('.')[0];

  return {
    balance: 0,
    id: itemId,
    price: details['price'],
    maxSupply: details['maxSupply'],
    name: details['name'],
    imageUrl: imageUrl.replace('https://farmland-game.github.io/assets/', './assets/'),
    animationUrl: details['animationUrl'].replace('https://farmland-game.github.io/assets/', './assets/'),
    paymentContract: details['paymentContract'],
    mintingActive: details['mintingActive'],
    rarity: details['rarity'].toNumber(),
    itemType: details['itemType'].toNumber()
  };
}
