import React, { Component } from 'react';
import { ICharacterDetails } from '../../models/CharacterDetails';
import SwiperCore, { Navigation } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react/swiper-react.js';
import 'swiper/swiper-bundle.min.css';
import 'swiper/swiper.scss';
import 'swiper/modules/navigation/navigation.scss';

import { IQuestService } from '../../services/QuestService';
import { Explorer } from './Explorer';
import { ItemService } from '../../services/ItemService';
import { EthereumService } from '../../services/EthereumService';
import { IQuestCharacter } from './Quests';

export interface IQuestProps {
  questService: IQuestService;
  characters: ICharacterDetails[];
  itemService?: ItemService;
  ethereumService: EthereumService;
  refreshedAtBlock: number;
  onBeginQuest: (questCharacter: IQuestCharacter) => void;
  onRestoreHealth: (questCharacter: IQuestCharacter) => void;
}

export interface IQuestState {
  characters: ICharacterDetails[];
  employ?: ICharacterDetails | null;
}

export class Quest extends Component<IQuestProps, IQuestState> {
  navigationPrevRef: any;
  navigationNextRef: any;

  constructor(props: any) {
    super(props);
    this.state = {
      characters: this.props.characters.sort((a, b) => (a.name < b.name ? -1 : 1)),
      employ: null
    };

    this.navigationPrevRef = React.createRef();
    this.navigationNextRef = React.createRef();

    SwiperCore.use([Navigation]);
  }

  static getDerivedStateFromProps(nextProps: IQuestProps, prevState: IQuestState) {
    var hasNoCharacterChanges = (a: ICharacterDetails[], b: ICharacterDetails[]) =>
      a.length === b.length && a.every(ac => b.some(bc => ac.id === bc.id));

    if (!hasNoCharacterChanges(nextProps.characters, prevState.characters)) {
      return { characters: nextProps.characters.sort((a, b) => (a.name < b.name ? -1 : 1)) };
    } else return null; // Triggers no change in the state
  }

  render() {
    return (
      <div className="characters">
        <Swiper className="px-1" navigation spaceBetween={10} slidesPerView="auto" centerInsufficientSlides>
          {this.state.characters.map(c => (
            <SwiperSlide key={c.name + c.index}>
              <Explorer
                key={c.index}
                itemService={this.props.itemService}
                questService={this.props.questService}
                character={c}
                ethereumService={this.props.ethereumService}
                onBeginQuest={this.props.onBeginQuest}
                refreshedAtBlock={this.props.refreshedAtBlock}
                onRestoreHealth={this.props.onRestoreHealth}></Explorer>
            </SwiperSlide>
          ))}
        </Swiper>
      </div>
    );
  }
}
