import { BigNumber, ethers } from 'ethers';
import { IRewardConfig } from '../models/ClaimConfig';
import { IPackDetails, toPackDetails } from '../models/PackDetails';
import { EthereumService } from './EthereumService';
import { MessagingService } from './MessagingService';
import { Utils } from './Utils';

export class PackService {
  walletAddress?: string;
  contract?: ethers.Contract;
  allPacks: IPackDetails[] = [];
  packMap: Map<number, IPackDetails> = new Map<number, IPackDetails>();

  constructor(
    public config: IRewardConfig,
    private messagingService: MessagingService,
    private ethereumService: EthereumService
  ) {
    this.walletAddress = this.ethereumService!.walletData.walletAddress;
    this.contract = new ethers.Contract(this.config.contractAddress, this.config.contractAbi, ethereumService.signer);

    this.loadStatics();
    this.loadClaims();
    this.setupEventHandlers();
  }

  async loadStatics() {
    if (this.allPacks.length > 0) {
      return;
    }

    var packs: IPackDetails[] = [];
    var packsJson = localStorage.getItem('packs');
    var packsLoadedContract = localStorage.getItem('packsLoadedContract');
    var packsLoadedMs = localStorage.getItem('packsLoaded') ?? Date.now();

    if (!packsJson || packsLoadedContract !== this.config.contractAddress || Date.now() - +packsLoadedMs > 86400000) {
      var p1 = await this.contract!.packs(1);
      var packDetails = await toPackDetails(1, p1);
      packs.push(packDetails);
      localStorage.setItem('packs', JSON.stringify(packs));
      localStorage.setItem('packsLoadedContract', this.config.contractAddress);
      localStorage.setItem('packsLoaded', Date.now().toString());
    } else {
      packs = JSON.parse(packsJson);
    }

    this.allPacks = packs.filter(i => i.name);

    await packs.forEach(async (pack: any) => {
      this.packMap = this.packMap.set(pack.id, pack);
    });
  }

  async loadClaims() {
    await this.allPacks.forEach(async (pack: IPackDetails) => {
      var claim = await this.getClaimBalance(pack.id);
      pack.balance = +Utils.formatUnits(claim, 0);
    });
  }

  async getClaimBalance(packId: number): Promise<any> {
    if (!this.contract) {
      return;
    }

    var walletAddress = this.ethereumService.walletData.walletAddress;

    var merkle = this.config!.claimMerkle.claims[walletAddress];

    if (!merkle) {
      return;
    }

    const claimed = await this.contract.isClaimed(merkle.index, packId);

    if (claimed) {
      return BigNumber.from(0);
    } else {
      return merkle.amount;
    }
  }

  claim(packId: number) {
    var walletAddress = this.ethereumService.walletData.walletAddress;

    if (!this.contract) {
      this.messagingService.errors.next({
        message: `Error: unable to load claim contract`,
        error: 'Unable to load claim contract'
      });
      return Promise.reject('Error: unable to load claim contract');
    }

    var pack = this.packMap.get(packId);

    if (!pack) {
      this.messagingService.errors.next({
        message: `Error: unable to load pack`,
        error: 'Unable to load pack'
      });
      return Promise.reject('Error: unable to load pack');
    }

    if (pack.balance <= 0) {
      this.messagingService.errors.next({
        message: `Error: nothing to claim`,
        error: 'Nothing to claim'
      });
      return Promise.reject('Error: nothing to claim');
    }

    var merkle = this.config!.claimMerkle.claims[walletAddress];

    if (!merkle) {
      this.messagingService.errors.next({
        message: `Error: address not found in merkle root`,
        error: 'Address not found in merkle root'
      });
      return Promise.reject('Error: address not found in merkle root');
    }

    return this.contract.claimAirDrop(merkle.index, merkle.proof, merkle.amount, pack.id).then(
      (response: any) =>
        this.messagingService.events.next({
          message: `Pack claim is pending`,
          pending: true
        }),
      (error: any) =>
        this.messagingService.errors.next({
          message: `Error during claim : (${Utils.getContractErrorMessage(error)})`,
          error: error
        })
    );
  }

  removeEventHandlers() {
    if (this.contract) {
      this.contract!.removeAllListeners();
    }
  }

  setupEventHandlers() {
    this.removeEventHandlers();

    var update = (message: string) =>
      this.loadClaims().then(() => {
        this.messagingService.events.next({ message: message });
      });

    if (this.contract) {
      this.contract!.on(
        {
          address: this.ethereumService.walletData.walletAddress,
          topics: [ethers.utils.id('Claimed(uint256,address,uint256)')]
        },
        (index, account, amount) => {
          if (account === this.ethereumService.walletData.walletAddress) {
            update(`Reward claimed`);
          }
        }
      );
    }
  }
}
