import { BigNumber, ethers } from 'ethers';
import { IQuestConfig } from '../models/CharacterConfig';
import { ICharacterDetails, ICharacterHealth, loadCharacterDetailsFromUri } from '../models/CharacterDetails';
import { QuestType } from '../models/Enums';
import { IItemDetails } from '../models/ItemDetails';
import { EthereumService } from './EthereumService';
import { MessagingService } from './MessagingService';
import { Utils } from './Utils';
import { Mutex } from 'async-mutex';

// need to do work on un-equipping and how supporting already equipped items
// page refresh (test end to end what does and doesn't refresh)
export interface IQuestStatics {
  name: string;
  itemSet: number;
  questPrice: BigNumber;
  chanceOfFindingLand: number;
  paymentAddress: string;
  questDuration: number;
  maxNumberOfQuestsBase: number;
  active: boolean;
}

export interface IItemsReceivedDetails {
  account: string;
  blockNumber: number;
  itemAmount: BigNumber;
  itemID: BigNumber;
  landAmount: BigNumber;
  quest: BigNumber;
  tokenID: BigNumber;
}

function areItemsReceivedDetailsEqual(a: IItemsReceivedDetails, b: IItemsReceivedDetails): boolean {
  return (
    a.account === b.account &&
    a.blockNumber === b.blockNumber &&
    a.itemAmount?.eq(b.itemAmount) &&
    a.itemID?.eq(b.itemID) &&
    a.landAmount?.eq(b.landAmount) &&
    a.quest?.eq(b.quest) &&
    a.tokenID?.eq(b.tokenID)
  );
}

export interface IQuestService {
  questConfig: IQuestConfig;
  questItems: Map<string, BigNumber[]>;
  questStatics?: IQuestStatics;
  isPaymentApproved: boolean;
  isQuestItemUseApproved: boolean;
  isHealthItemUseApproved: boolean;
  update(): Promise<any>;
  approvePayment(): Promise<any>;
  approveQuestItemUse(): Promise<any>;
  approveHealthItemUse(): Promise<any>;
  getCharacters(): Promise<ICharacterDetails[]>;
  getQuestState(characterDetails: ICharacterDetails): Promise<[length: number, endblock: number, active: boolean]>;
  getMaxQuests(characterDetails: ICharacterDetails): Promise<number>;
  getQuestDuration(characterDetails: ICharacterDetails): Promise<number>;
  beginQuest(characterDetails: ICharacterDetails, questLength: number, items: number[]): Promise<any>;
  completeQuest(characterDetails: ICharacterDetails, items: number[]): Promise<any>;
  releaseExplorer(characterDetails: ICharacterDetails): Promise<any>;
  getCharactersItemsReceived(characterDetails: ICharacterDetails): Promise<IItemsReceivedDetails[]>;
  getWalletItemsFound(fromBlock: number): Promise<IItemsReceivedDetails[]>;
  getCharactersHealth(characterDetails: ICharacterDetails): Promise<ICharacterHealth>;
  restoreHealth(characterDetails: ICharacterDetails, item: IItemDetails): Promise<any>;
}

abstract class QuestServiceBase implements IQuestService {
  private mutex = new Mutex();
  protected historicItemMap: Map<number, IItemsReceivedDetails[]> = new Map<number, IItemsReceivedDetails[]>();
  protected walletAddress: string;
  protected questContract: ethers.Contract;
  protected paymentContract?: ethers.Contract;
  protected itemContract?: ethers.Contract;
  protected itemSetsContract?: ethers.Contract;
  protected characterContract: ethers.Contract;
  protected activityContract?: ethers.Contract;
  protected healthManagerContract?: ethers.Contract;

  // TODO: is this needed
  protected maxStatBoosts: number = 0;
  public questStatics?: IQuestStatics;
  public isPaymentApproved: boolean = false;
  public isQuestItemUseApproved: boolean = false;
  public isHealthItemUseApproved: boolean = false;
  public questItems: Map<string, BigNumber[]> = new Map<string, BigNumber[]>();

  constructor(
    public questConfig: IQuestConfig,
    protected messagingService: MessagingService,
    protected ethereumService: EthereumService
  ) {
    this.walletAddress = this.ethereumService!.walletData.walletAddress;

    this.questContract = new ethers.Contract(
      this.questConfig.contractAddress,
      this.questConfig.contractContants.contractAbi,
      ethereumService.signer
    );

    this.itemContract = new ethers.Contract(
      this.ethereumService.networkManager!.network!.itemConfig!.contractAddress,
      this.ethereumService.networkManager!.network!.itemConfig!.assetConstants!.contractAbi,
      ethereumService.signer
    );

    this.itemSetsContract = new ethers.Contract(
      this.ethereumService.networkManager!.network!.itemSetsConfig!.contractAddress,
      this.ethereumService.networkManager!.network!.itemSetsConfig!.contractConstants!.contractAbi,
      ethereumService.signer
    );

    this.characterContract = new ethers.Contract(
      this.ethereumService.networkManager!.network!.characterConfig!.contractAddress,
      this.ethereumService.networkManager!.network!.characterConfig!.contractContants.contractAbi,
      ethereumService.signer
    );

    this.activityContract = new ethers.Contract(
      this.ethereumService.networkManager!.network!.characterConfig!.activityConfig.contractAddress,
      this.ethereumService.networkManager!.network!.characterConfig!.activityConfig.contractConstants!.contractAbi,
      this.ethereumService.signer!
    );

    this.healthManagerContract = new ethers.Contract(
      this.ethereumService.networkManager!.network!.characterConfig!.healthManagerConfig.contractAddress,
      this.ethereumService.networkManager!.network!.characterConfig!.healthManagerConfig.contractConstants!.contractAbi,
      this.ethereumService.signer!
    );

    this.paymentContract = new ethers.Contract(
      this.questConfig!.questPayConfig.contractAddress,
      this.questConfig!.questPayConfig.assetConstants!.contractAbi,
      ethereumService.signer
    );

    this.loadStatics();
    this.update();
    this.setupEventHandlers();
  }

  async loadStatics() {
    this.questStatics = await this.questContract.quests(this.questConfig.questIndex);
    this.maxStatBoosts = await this.characterContract.maxTraitBoosts();

    var itemTypes = await this.itemContract!.getItemTypes();

    itemTypes.forEach(async (itemType: string, index: number) => {
      var itemTypeItems = await this.itemSetsContract!.getItemsBySetAndType(this.questStatics!.itemSet, index);
      this.questItems.set(
        itemType,
        itemTypeItems.map((i: any) => i.itemID)
      );
    });
  }

  async update(): Promise<any> {
    this.isPaymentApproved = await this.paymentContract!.isOperatorFor(
      this.questContract!.address,
      this.ethereumService.walletData.walletAddress
    );

    this.isQuestItemUseApproved = await this.itemContract!.isApprovedForAll(
      this.ethereumService.walletData.walletAddress,
      this.questContract!.address
    );

    this.isHealthItemUseApproved = await this.itemContract!.isApprovedForAll(
      this.ethereumService.walletData.walletAddress,
      this.healthManagerContract!.address
    );

    await this.mutex.runExclusive(async () => {
      await this.refreshHistoricItems();
    });
  }

  async refreshHistoricItems() {
    var questItems: IItemsReceivedDetails[] = [];
    var questItemsJson = localStorage.getItem('questItems');
    var questContract = localStorage.getItem('questContract');
    var questItemsBlock = localStorage.getItem('questItemsBlock') ?? this.ethereumService.provider!.blockNumber;

    if (questContract !== this.questConfig.contractAddress) {
      questItemsBlock = 13040816;
    } else if (questItemsJson) {
      questItems = JSON.parse(questItemsJson);

      if (this.historicItemMap.size === 0) {
        this.updateHistoricItemMap(questItems);
      }
    }

    if (!questItemsJson || this.ethereumService.provider!.blockNumber > +questItemsBlock) {
      var latestItems = await this.getWalletItemsFound(+questItemsBlock);

      latestItems.forEach((i: IItemsReceivedDetails) => {
        if (!questItems.some((j: IItemsReceivedDetails) => areItemsReceivedDetailsEqual(i, j))) {
          questItems.push(i);
        }
      });

      if (latestItems.length > 0) {
        this.updateHistoricItemMap(questItems);
      }

      localStorage.setItem('questItems', JSON.stringify(questItems));
      localStorage.setItem('questContract', this.questConfig.contractAddress);
    }

    localStorage.setItem('questItemsBlock', this.ethereumService.provider!.blockNumber.toString());
  }

  updateHistoricItemMap(questItems: IItemsReceivedDetails[]) {
    this.historicItemMap = questItems.reduce(
      (acc: Map<number, IItemsReceivedDetails[]>, curr: IItemsReceivedDetails) => {
        var characterIndex = BigNumber.from(curr.tokenID).toNumber();
        var items = acc.get(characterIndex);
        if (items) {
          acc.set(characterIndex, items.concat(curr));
        } else {
          acc.set(characterIndex, [curr]);
        }
        return acc;
      },
      new Map<number, IItemsReceivedDetails[]>()
    );
  }

  async approvePayment(): Promise<any> {
    return this.paymentContract!.authorizeOperator(this.questContract!.address, {
      from: this.ethereumService.walletData.walletAddress
    }).then(
      () => this.messagingService.events.next({ message: 'Quest payment approval is pending', pending: true }),
      (error: any) =>
        this.messagingService.errors.next({
          message: `Error approving quest payment : (${Utils.getContractErrorMessage(error)})`,
          error: error
        })
    );
  }

  async approveQuestItemUse(): Promise<any> {
    return this.itemContract!.setApprovalForAll(this.questContract!.address, true).then(
      () => this.messagingService.events.next({ message: 'Quest item use approval is pending', pending: true }),
      (error: any) =>
        this.messagingService.errors.next({
          message: `Error approving quest item use : (${Utils.getContractErrorMessage(error)})`,
          error: error
        })
    );
  }

  async approveHealthItemUse(): Promise<any> {
    return this.itemContract!.setApprovalForAll(this.healthManagerContract!.address, true).then(
      () => this.messagingService.events.next({ message: 'Health item use approval is pending', pending: true }),
      (error: any) =>
        this.messagingService.errors.next({
          message: `Error approving health item use : (${Utils.getContractErrorMessage(error)})`,
          error: error
        })
    );
  }

  abstract getCharacters(): Promise<ICharacterDetails[]>;

  async getQuestState(
    characterDetails: ICharacterDetails
  ): Promise<[questlength: number, endblock: number, active: boolean]> {
    const activity = await this.activityContract!.charactersActivity(characterDetails.id);
    return [
      BigNumber.from(activity.activityDuration).toNumber(),
      BigNumber.from(activity.endBlock).toNumber(),
      activity.active
    ];
  }

  getMaxQuests(characterDetails: ICharacterDetails): Promise<number> {
    return this.questContract.getCharactersMaxQuests(characterDetails.id, this.questStatics?.maxNumberOfQuestsBase);
  }

  getQuestDuration(characterDetails: ICharacterDetails): Promise<number> {
    return this.questContract.getCharactersQuestDuration(characterDetails.id, this.questStatics?.questDuration);
  }

  abstract beginQuest(characterDetails: ICharacterDetails, questLength: number, items: number[]): Promise<any>;

  abstract completeQuest(characterDetails: ICharacterDetails, items: number[]): Promise<any>;

  abstract releaseExplorer(characterDetails: ICharacterDetails): Promise<any>;

  async getCharactersItemsReceived(characterDetails: ICharacterDetails): Promise<IItemsReceivedDetails[]> {
    return this.historicItemMap.get(characterDetails.id) ?? [];
  }

  async getWalletItemsFound(fromBlock: number): Promise<IItemsReceivedDetails[]> {
    var items: any[] = [];
    try {
      items = await this.questContract!.queryFilter(
        this.questContract!.filters.ItemFound(this.ethereumService.walletData.walletAddress),
        fromBlock,
        this.ethereumService.provider!.blockNumber
      );
    } catch (e) {
      console.log(e);
    }

    if (items.length > 0) {
      this.messagingService.events.next({
        message: `Your explorer's discoveries have been added to your barn`
      });
    }

    return items
      .filter((i: any) => i.args['account'] === this.walletAddress)
      .map<IItemsReceivedDetails>((i: any) => {
        return {
          account: i.args['account'],
          blockNumber: i.blockNumber,
          itemAmount: i.args['amountMinted'],
          itemID: i.args['itemMinted'],
          landAmount: i.args['landMinted'],
          quest: i.args['quest'],
          tokenID: i.args['tokenID']
        };
      });
  }

  abstract getCharactersHealth(characterDetails: ICharacterDetails): Promise<ICharacterHealth>;

  abstract restoreHealth(characterDetails: ICharacterDetails, item: IItemDetails): Promise<any>;

  protected async loadCharacter(id: any, index: number): Promise<ICharacterDetails | null> {
    try {
      const tokenUri = await this.characterContract!.tokenURI(id.toNumber());
      const stats = await this.characterContract!.collectibleTraits(id);
      const boostsRemaining =
        this.maxStatBoosts - (await this.characterContract!.collectibleTraitBoostTracker(id.toNumber()));
      return await loadCharacterDetailsFromUri(index, id, tokenUri, stats, boostsRemaining);
    } catch (e) {
      return null;
    }
  }

  protected async loadCharacters(characters: any[]) {
    var n: ICharacterDetails[] = [];

    var i = 0;
    for (const c of characters) {
      var character = await this.loadCharacter(c, i++);

      if (character && character.revealed) {
        n.push(character);
      }
    }

    return n;
  }

  removeEventHandlers() {
    if (this.questContract) {
      this.questContract!.removeAllListeners();
    }
  }

  setupEventHandlers() {
    this.removeEventHandlers();

    if (this.questContract) {
      this.questContract!.on(
        {
          address: this.ethereumService.walletData.walletAddress,
          topics: [ethers.utils.id('QuestStarted(address,uint256,uint256,uint256)')]
        },
        (sender, quest, endBlockNumber, tokenId) => {
          if (sender === this.ethereumService.walletData.walletAddress) {
            this.messagingService.events.next({ message: `Your character has started exploring` });
          }
        }
      );

      this.questContract!.on(
        {
          address: this.ethereumService.walletData.walletAddress,
          topics: [ethers.utils.id('QuestCompleted(address,uint256,uint256,uint256)')]
        },
        (sender, quest, tokenID, landClaimed) => {
          if (sender === this.ethereumService.walletData.walletAddress) {
            this.messagingService.events.next({ message: `Your character has completed the quest` });
          }
        }
      );
    }
  }
}

export class ExplorerQuestService extends QuestServiceBase {
  async getCharacters(): Promise<ICharacterDetails[]> {
    var questCharacters = await this.questContract!.getCharactersByAccount(this.walletAddress!);
    return await this.loadCharacters(questCharacters);
  }

  async beginQuest(characterDetails: ICharacterDetails, questLength: number, items: number[]): Promise<any> {
    var sanitizedQuestLength = Utils.sanitizeParseUnits(questLength);

    return this.questContract!.beginQuest(
      this.questConfig.questIndex,
      characterDetails.id,
      sanitizedQuestLength,
      items.length ? true : false,
      items.length ? items[0] : 0
    ).then(
      () =>
        this.messagingService.events.next({
          message: `${this.questConfig.questType} begin quest is pending`,
          pending: true
        }),
      (error: any) =>
        this.messagingService.errors.next({
          message: `Error beginning ${this.questConfig.questType} quest : (${Utils.getContractErrorMessage(error)})`,
          error: error
        })
    );
  }

  async completeQuest(characterDetails: ICharacterDetails, items: number[]): Promise<any> {
    return this.questContract!.completeQuest(this.questConfig.questIndex, characterDetails.id).then(
      () =>
        this.messagingService.events.next({
          message: `${this.questConfig.questType} complete quest is pending`,
          pending: true
        }),
      (error: any) =>
        this.messagingService.errors.next({
          message: `Error completing ${this.questConfig.questType} quest : (${Utils.getContractErrorMessage(error)})`,
          error: error
        })
    );
  }

  async releaseExplorer(characterDetails: ICharacterDetails): Promise<any> {
    return this.questContract!.releaseCharacter(characterDetails.id).then(
      () =>
        this.messagingService.events.next({
          message: `${this.questConfig.questType} release is pending`,
          pending: true
        }),
      (error: any) =>
        this.messagingService.errors.next({
          message: `Error releasing ${this.questConfig.questType} : (${Utils.getContractErrorMessage(error)})`,
          error: error
        })
    );
  }

  async getCharactersHealth(characterDetails: ICharacterDetails): Promise<ICharacterHealth> {
    var max = await this.activityContract!.getCharactersMaxHealth(characterDetails.id);
    try {
      var remaining = await this.activityContract!.calculateHealth(characterDetails.id);
    } catch (e) {}

    return {
      max: BigNumber.from(max).toNumber(),
      remaining: remaining ? BigNumber.from(remaining).toNumber() : BigNumber.from(max).toNumber()
    };
  }

  async restoreHealth(characterDetails: ICharacterDetails, item: IItemDetails): Promise<any> {
    await this.healthManagerContract!.restoreHealth(characterDetails.id, 0, item.id);
  }
}

export class QuestServiceFactory {
  static createQuests(messagingService: MessagingService, ethereumService: EthereumService): IQuestService[] {
    if (!ethereumService.networkManager?.network?.questConfigs) {
      return [];
    }

    return ethereumService.networkManager!.network!.questConfigs.map(qc => {
      return QuestServiceFactory.create(qc, messagingService, ethereumService);
    });
  }

  private static create(
    questConfig: IQuestConfig,
    messagingService: MessagingService,
    ethereumService: EthereumService
  ): IQuestService {
    switch (questConfig.questType) {
      case QuestType.Explorer:
        return new ExplorerQuestService(questConfig, messagingService, ethereumService);
    }
  }
}
