import React, { Component } from 'react';
import { ICharacterDetails, ICharacterHealth, getHealthPercent, isHealthFull } from '../../models/CharacterDetails';
import { IQuestService } from '../../services/QuestService';
import { IItemDetails } from '../../models/ItemDetails';
import './RestoreHealth.scss';

import SwiperCore, { Navigation } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react/swiper-react.js';
import 'swiper/swiper-bundle.min.css';
import 'swiper/swiper.scss';
import 'swiper/modules/navigation/navigation.scss';
import { ItemService } from '../../services/ItemService';

export interface IRestoreHealthProps {
  questService: IQuestService;
  itemService: ItemService;
  character: ICharacterDetails;
  health: ICharacterHealth;
  close: any;
}

export interface IRestoreHealthState {
  selectedItem: IItemDetails | null;
}

export class RestoreHealth extends Component<IRestoreHealthProps, IRestoreHealthState> {
  healthItems: IItemDetails[];

  constructor(props: any) {
    super(props);

    this.state = {
      selectedItem: null
    };

    this.selectHealthItem = this.selectHealthItem.bind(this);
    this.restoreHealth = this.restoreHealth.bind(this);
    this.close = this.close.bind(this);

    this.healthItems = this.props
      .questService!.questItems.get('Health')!
      .map(id => this.props.itemService!.itemMap.get(id.toNumber())!);

    SwiperCore.use([Navigation]);
  }

  selectHealthItem(healthItem: IItemDetails): void {
    this.setState({ selectedItem: healthItem });
  }

  restoreHealth(healthItem: IItemDetails): void {
    this.props.questService.restoreHealth(this.props.character, healthItem);
    this.close();
  }

  close() {
    this.props.close();
  }

  render() {
    return (
      <div className="is-active modal noselect">
        <div className="modal-background" onClick={this.close}></div>
        <div id="restore-health" className="modal-content restore-health">
          <div
            className="card"
            style={{
              backgroundImage: `linear-gradient(rgba(10, 10, 10, 0.6), rgba(10, 10, 10, 1)),url(${
                this.props.character!.image
              })`
            }}>
            <button className="modal-close is-large" aria-label="close" onClick={this.close}></button>
            <div className="is-flex is-justify-content-center restore-health-title pb-3">
              {this.props.character.name}
            </div>
            <div className="restore-health-content">
              <div className="is-flex is-flex-direction-column is-align-items-center is-justify-content-center mt-1">
                {this.renderHealth()}
              </div>

              <div
                className="is-flex is-flex-direction-column is-align-items-center is-justify-content-center mt-5"
                style={{ overflow: 'hidden' }}>
                {this.renderItems()}
              </div>

              <div className="is-flex is-flex-direction-column is-align-items-stretch is-justify-content-center character-stats mt-5">
                <button
                  className="button restore-button is-action"
                  onClick={() => this.restoreHealth(this.healthItems[0])}
                  disabled={
                    !this.state.selectedItem || this.healthItems.length <= 0 || isHealthFull(this.props.health)
                  }>
                  {isHealthFull(this.props.health) && <span>Health is Full</span>}
                  {!isHealthFull(this.props.health) && this.state.selectedItem && (
                    <span>Use {this.state.selectedItem.name}</span>
                  )}
                  {!isHealthFull(this.props.health) && !this.state.selectedItem && <span>Select an Item</span>}
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  renderHealth() {
    return (
      <React.Fragment>
        {this.props.health && (
          <div className="health-container">
            <div
              className={`health-bar ${isHealthFull(this.props.health) ? 'health-full' : ''}`}
              style={{
                background: `linear-gradient(to right, var(--red) 0%, var(--green) ${
                  65 + (100 - getHealthPercent(this.props.health))
                }%)`,
                width: `${getHealthPercent(this.props.health)}%`
              }}>
              Health
            </div>
          </div>
        )}
      </React.Fragment>
    );
  }

  renderItems() {
    if (!this.props.questService.isHealthItemUseApproved) {
      return (
        <React.Fragment>
          <span className="column meme is-half has-text-centered">
            <h3 className="empty-items py-3">Using Health Items Not Approved</h3>
          </span>
        </React.Fragment>
      );
    }

    var availableItems = this.healthItems.filter(i => i.balance > 0);

    if (availableItems.length > 0) {
      return (
        <React.Fragment>
          <Swiper
            navigation={{
              prevEl: '.swiper-health-prev',
              nextEl: '.swiper-health-next'
            }}
            spaceBetween={20}
            slidesPerView="auto">
            {availableItems.map(i => (
              <SwiperSlide key={i.id}>
                <div
                  className={`health-item ${this.state.selectedItem?.id === i.id ? 'health-item-selected' : ''}`}
                  onClick={() => {
                    this.selectHealthItem(i);
                  }}>
                  <img alt={i.name} className="token" src={i.imageUrl} />
                  <span className="item-name">{i.name}</span>
                  {i.balance > 0 && <span className="item-balance pl-2">{i.balance}</span>}
                  {!i.balance && <span className="item-balance pl-2">-</span>}
                </div>
              </SwiperSlide>
            ))}
            {/* <div className="swiper-button-prev swiper-health-prev"></div>
            <div className="swiper-button-next swiper-health-next"></div> */}
          </Swiper>
        </React.Fragment>
      );
    } else {
      return (
        <span className="column meme is-half has-text-centered">
          <h3 className="empty-items py-3">No Health Items</h3>
        </span>
      );
    }
  }
}
