import React, { Component } from 'react';
// @ts-ignore
import { Subscription } from 'rxjs';
import { EthereumService } from '../../services/EthereumService';
import { MessagingService } from '../../services/MessagingService';
import '../Content.scss';
import '../Tables.scss';
import './Barn.scss';
// @ts-ignore
import CollapsibleCard from '../Common/CollapsibleCard';
import { ItemService } from '../../services/ItemService';
import { IItemDetails } from '../../models/ItemDetails';
import { Unrevealed, UnrevealedS } from '../Characters/Unrevealed';
import { PackService } from '../../services/PackService';
import { IPackDetails } from '../../models/PackDetails';
import { Utils } from '../../services/Utils';

enum AssetsToDisplay {
  All,
  Wallet
}

interface IBarnProps {
  messagingService: MessagingService;
  ethereumservice: EthereumService;
}

interface IBarnState {
  materials: IItemDetails[];
  items: IItemDetails[];
  showDetail: boolean;
  assetsDisplayed: AssetsToDisplay;
  zoomedItem: IItemDetails | null;
  zoomedPack: IPackDetails | null;
}

class Barn extends Component<IBarnProps, IBarnState> {
  subscription: Subscription;
  walletAddress?: string;
  chainId?: number;
  itemService?: ItemService;
  packService?: PackService;

  constructor(props: IBarnProps) {
    super(props);

    var toDisplay = localStorage.getItem('barnAssetsToDisplay');

    this.state = {
      materials: [],
      items: [],
      showDetail: false,
      assetsDisplayed: toDisplay === 'Wallet' ? AssetsToDisplay.Wallet : AssetsToDisplay.All,
      zoomedItem: null,
      zoomedPack: null
    };

    this.subscription = new Subscription();

    this.setZoomedItem = this.setZoomedItem.bind(this);
    this.setZoomedPack = this.setZoomedPack.bind(this);
    this.openPack = this.openPack.bind(this);
    this.toggleAssetsDisplayed = this.toggleAssetsDisplayed.bind(this);
  }

  componentDidMount() {
    var eventSubscription = this.props.messagingService.events.subscribe(event => {
      this.refresh();
    });

    this.subscription.add(eventSubscription);

    this.refresh();
  }

  componentWillUnmount() {
    this.subscription.unsubscribe();
  }

  setZoomedItem(item: IItemDetails | null) {
    this.setState({ zoomedItem: item });
  }

  setZoomedPack(pack: IPackDetails | null) {
    this.setState({ zoomedPack: pack });
  }

  openPack(pack: IPackDetails) {
    this.packService?.claim(pack.id);
  }

  toggleAssetsDisplayed() {
    this.setState({
      assetsDisplayed:
        this.state.assetsDisplayed === AssetsToDisplay.Wallet ? AssetsToDisplay.All : AssetsToDisplay.Wallet
    });

    localStorage.setItem(
      'barnAssetsToDisplay',
      AssetsToDisplay[
        this.state.assetsDisplayed === AssetsToDisplay.Wallet ? AssetsToDisplay.All : AssetsToDisplay.Wallet
      ]
    );
  }

  async refresh() {
    if (
      this.props.ethereumservice.walletData.walletAddress &&
      this.props.ethereumservice.networkManager?.network?.chainId
    ) {
      if (
        this.props.ethereumservice.walletData.walletAddress !== this.walletAddress ||
        this.props.ethereumservice.networkManager!.network?.chainId !== this.chainId
      ) {
        this.walletAddress = this.props.ethereumservice.walletData.walletAddress;
        this.chainId = this.props.ethereumservice.networkManager!.network!.chainId;
        await this.init();
      } else {
        await this.update();
      }
    }
  }

  async init(): Promise<any> {
    if (
      this.props.ethereumservice.networkManager?.network &&
      this.props.ethereumservice.networkManager?.network?.assets.size > 0
    ) {
      await this.loadItems();
      await this.loadRewards();

      await this.update();
    }
  }

  async loadItems(): Promise<any> {
    if (!this.props.ethereumservice.networkManager!.network!.itemConfig) {
      return;
    }

    this.itemService = new ItemService(
      this.props.ethereumservice.networkManager!.network!.itemConfig!,
      this.props.messagingService,
      this.props.ethereumservice
    );
  }

  async loadRewards(): Promise<any> {
    if (!this.props.ethereumservice.networkManager!.network!.rewardConfig) {
      return;
    }

    this.packService = new PackService(
      this.props.ethereumservice.networkManager!.network!.rewardConfig!,
      this.props.messagingService,
      this.props.ethereumservice
    );
  }

  async update(): Promise<any> {
    if (this.itemService) {
      await this.itemService!.loadOwned();

      var materials = this.itemService!.allItems.filter((i: IItemDetails) => i.itemType === 0);
      var items = this.itemService!.allItems.filter((i: IItemDetails) => i.itemType !== 0);

      this.setState({ materials: materials, items: items });
    }

    if (this.packService) {
      await this.packService!.loadClaims();
    }
  }

  render() {
    return (
      <section id="content" className="barn is-vcentered">
        {this.renderZoomedItem()}
        {this.renderZoomedPack()}
        <div className="container">
          {!this.props.ethereumservice.walletData.isConnected && (
            <span
              className="column meme is-half has-text-centered mt-4"
              style={{ cursor: 'pointer' }}
              onClick={() => this.props.ethereumservice.connectWallet()}>
              <h3 className="sub-title py-3">Connect Wallet</h3>
            </span>
          )}
          {this.props.ethereumservice.walletData.isConnected && (
            <React.Fragment>
              {!this.itemService && (
                <span className="column meme is-half has-text-centered mt-4">
                  <h3 className="sub-title py-3">Coming Soon!</h3>
                </span>
              )}
              {this.itemService && (
                <div className="tile is-ancestor mx-3">
                  <div className="tile is-vertical is-12 mb-6">
                    <div className="field pl-1">
                      <div>
                        <input
                          id="assetsDisplayed"
                          type="checkbox"
                          name="assetsDisplayed"
                          className={`${
                            this.state.assetsDisplayed === AssetsToDisplay.Wallet ? 'is-active' : ''
                          }  switch is-rounded is-small`}
                          onClick={() => this.toggleAssetsDisplayed()}
                          defaultChecked={this.state.assetsDisplayed === AssetsToDisplay.Wallet}
                        />
                        <label htmlFor="assetsDisplayed">
                          <span className="has-light-text noselect" style={{ marginTop: '-1px' }}>
                            Hide zero balances
                          </span>
                        </label>
                      </div>
                    </div>

                    {this.state.assetsDisplayed === AssetsToDisplay.All && (
                      <React.Fragment>
                        {this.state.materials.length > 0 && (
                          <CollapsibleCard title="MATERIALS" id="materials-card" count={this.state.materials.length}>
                            {this.renderMaterials(this.state.materials)}
                          </CollapsibleCard>
                        )}
                        {this.state.items.length > 0 && (
                          <CollapsibleCard title="ITEMS" id="items-card" count={this.state.items.length}>
                            {this.renderItems(this.state.items)}
                          </CollapsibleCard>
                        )}
                        {this.state.items.length > 0 && (
                          <CollapsibleCard title="PACKS" id="items-card" count={this.state.items.length}>
                            {this.renderPacks()}
                          </CollapsibleCard>
                        )}
                      </React.Fragment>
                    )}

                    {this.state.assetsDisplayed === AssetsToDisplay.Wallet && (
                      <React.Fragment>
                        {this.state.materials.length > 0 && this.state.materials.filter(i => i.balance > 0) && (
                          <CollapsibleCard title="MATERIALS" id="materials-card" count={this.state.materials.length}>
                            {this.renderMaterials(this.state.materials)}
                          </CollapsibleCard>
                        )}
                        {this.state.items.length > 0 && this.state.items.filter(i => i.balance > 0) && (
                          <CollapsibleCard title="ITEMS" id="items-card" count={this.state.items.length}>
                            {this.renderItems(this.state.items)}
                          </CollapsibleCard>
                        )}
                        {this.state.items.length > 0 && (
                          <CollapsibleCard title="PACKS" id="items-card" count={this.state.items.length}>
                            {this.renderPacks()}
                          </CollapsibleCard>
                        )}
                      </React.Fragment>
                    )}
                  </div>
                </div>
              )}
            </React.Fragment>
          )}
        </div>
      </section>
    );
  }

  renderMaterials(items: IItemDetails[]) {
    var filtered = items
      .filter(i => this.state.assetsDisplayed === AssetsToDisplay.All || i.balance > 0)
      .sort((a, b) => a.id - b.id);

    if (filtered.length > 0) {
      return (
        <div className="grid mt-4">
          {filtered.map(i => (
            <div className="grid-item is-material" key={i.id} onClick={() => this.setZoomedItem(i)}>
              <img alt={i.name} className="token" src={i.imageUrl} />
              <span className="item-name">{i.name}</span>
              {i.balance > 0 && <span className="item-balance pl-2">{i.balance}</span>}
              {!i.balance && <span className="item-balance pl-2">-</span>}
            </div>
          ))}
        </div>
      );
    } else {
      return (
        <span className="column meme is-half has-text-centered mt-3">
          <h3 className="empty-items pt-3">Empty</h3>
        </span>
      );
    }
  }

  renderItems(items: IItemDetails[]) {
    var filtered = items
      .filter(i => this.state.assetsDisplayed === AssetsToDisplay.All || i.balance > 0)
      .sort((a, b) => a.id - b.id);

    if (filtered.length > 0) {
      return (
        <div className="grid mt-4">
          {filtered.map(i => (
            <div
              className={`grid-item is-item ${Utils.toRarityDisplay(i.rarity)}`}
              key={i.id}
              onClick={() => this.setZoomedItem(i)}>
              <div className="rarity-display">{Utils.toRarityDisplay(i.rarity)}</div>
              <img alt={i.name} className="token" src={i.imageUrl} />
              <span className="item-name">{i.name}</span>
              {i.balance > 0 && <span className="item-balance pl-2">{i.balance}</span>}
              {!i.balance && <span className="item-balance pl-2">-</span>}
            </div>
          ))}
        </div>
      );
    } else {
      return (
        <span className="column meme is-half has-text-centered mt-3">
          <h3 className="empty-items pt-3">Empty</h3>
        </span>
      );
    }
  }

  renderPacks() {
    var filtered = this.packService?.allPacks.filter(p => p.balance > 0);

    if (filtered && filtered.length > 0) {
      return (
        <div className="grid mt-4 mb-4">
          {filtered.map(p => (
            <div className="grid-item" onClick={() => this.setZoomedPack(p)}>
              <div className="pack-container">
                <UnrevealedS scale={0.35}></UnrevealedS>
              </div>
              <span className="item-name">{p!.name}</span>
              <span className="item-balance pl-2">{p!.balance}</span>
            </div>
          ))}
        </div>
      );
    } else {
      return (
        <span className="column meme is-half has-text-centered mt-3">
          <h3 className="empty-items pt-3">Empty</h3>
        </span>
      );
    }
  }

  renderZoomedItem() {
    return (
      <React.Fragment>
        {this.state.zoomedItem && (
          <div className="is-active modal">
            <div className="modal-background" onClick={() => this.setZoomedItem(null)}></div>
            <div className="modal-content characters zoomed-item">
              <button
                className="modal-close is-large"
                aria-label="close"
                onClick={() => this.setZoomedItem(null)}></button>
              <img alt={this.state.zoomedItem.name} className="token" src={this.state.zoomedItem.imageUrl} />
            </div>
          </div>
        )}
      </React.Fragment>
    );
  }

  renderZoomedPack() {
    return (
      <React.Fragment>
        {this.state.zoomedPack && (
          <div className="is-active modal">
            <div className="modal-background" onClick={() => this.setZoomedPack(null)}></div>
            <div className="modal-content characters zoomed-pack">
              <button
                className="modal-close is-large"
                aria-label="close"
                onClick={() => this.setZoomedPack(null)}></button>
              <Unrevealed></Unrevealed>

              <button className="button is-action mb-3 " onClick={() => this.openPack(this.state.zoomedPack!)}>
                {this.state.zoomedPack.balance > 1 && <span>Open {this.state.zoomedPack.name}s</span>}
                {this.state.zoomedPack.balance === 1 && <span>Open {this.state.zoomedPack.name}</span>}
              </button>
            </div>
          </div>
        )}
      </React.Fragment>
    );
  }
}

export default Barn;
