import React, { Component } from 'react';
import { EthereumService } from '../../services/EthereumService';
import { IQuestService } from '../../services/QuestService';
import { IQuestCharacter } from './Quests';
import { ICharacterDetails, ICharacterHealth } from '../../models/CharacterDetails';
import { Character } from '../Characters/Character';
import { ICharacterAction } from '../../models/ICharacterAction';
import { ItemService } from '../../services/ItemService';
import { IItemDetails } from '../../models/ItemDetails';
import Flip from '../Common/Flip';
import { Utils } from '../../services/Utils';
import { ITime } from '../../services/Interfaces';
import unknownItem from '../../assets/unknown-item.png';
import landIcon from '../../assets/land.png';
import { Unrevealed } from '../Characters/Unrevealed';
import './Explorer.scss';
import { BigNumber } from 'ethers';

interface IExplorerProps {
  questService?: IQuestService;
  character?: ICharacterDetails;
  itemService?: ItemService;
  ethereumService?: EthereumService;
  refreshedAtBlock: number;
  onBeginQuest: (questCharacter: IQuestCharacter) => void;
  onRestoreHealth: (questCharacter: IQuestCharacter) => void;
}

interface IExplorerItems {
  received: number;
  blockNumber: number;
  item: IItemDetails;
}

interface IExplorerState {
  explorerState: ExplorerState;
  recent: IExplorerItems[];
  landRecent: number;
  history: IExplorerItems[];
  landHistory: number;
  questLength: number;
  questActive: boolean;
  questCountdownSecs: number;
  questCountdownTime: ITime;
  dropdownActiveClass: string;
  health: ICharacterHealth;
  refreshedAtBlock: number;
}

enum ExplorerState {
  New,
  Exploring,
  Completed,
  Idle
}

export class Explorer extends Component<IExplorerProps, IExplorerState> {
  dropdownBody: React.RefObject<HTMLDivElement>;

  exploringAction?: ICharacterAction;
  releaseExplorerAction?: ICharacterAction;
  approveQuestItemUseAction?: ICharacterAction;
  restoreHealthAction?: ICharacterAction;
  aprovePaymentAction?: ICharacterAction;
  completeQuestAction?: ICharacterAction;
  beginQuestAction?: ICharacterAction;
  approveHealthItemUseAction?: ICharacterAction;

  constructor(props: IExplorerProps) {
    super(props);

    this.dropdownBody = React.createRef();

    this.state = {
      explorerState: ExplorerState.Idle,
      recent: [],
      landRecent: 0,
      history: [],
      landHistory: 0,
      questLength: 0,
      questActive: false,
      questCountdownSecs: 0,
      questCountdownTime: { days: '  ', hours: '  ', mins: '  ', secs: '  ', isComplete: false },
      dropdownActiveClass: '',
      health: { max: 0, remaining: 0 },
      refreshedAtBlock: props.refreshedAtBlock
    };

    this.aprovePayment = this.aprovePayment.bind(this);
    this.approveQuestItemUse = this.approveQuestItemUse.bind(this);
    this.approveHealthItemUse = this.approveHealthItemUse.bind(this);
    this.releaseExplorer = this.releaseExplorer.bind(this);
    this.beginQuest = this.beginQuest.bind(this);
    this.restoreHealth = this.restoreHealth.bind(this);
    this.completeQuest = this.completeQuest.bind(this);
    this.toggleDropdown = this.toggleDropdown.bind(this);

    this.buildActions();

    this.updateState(this.props);
  }

  buildActions() {
    this.exploringAction = {
      name: 'Exploring',
      isApproved: true,
      onClick: () => {},
      onHoverMessage: `${
        this.props.character!.name
      } is out exploring. You must wait for the quest to end before being able to do anything else.`,
      enabled: true
    };
    this.releaseExplorerAction = {
      name: 'Release Explorer',
      isApproved: true,
      onClick: () => this.releaseExplorer(),
      onHoverMessage: `Release ${
        this.props.character!.name
      } from employment as an Explorer, they will return to the Characters page`,
      enabled: true
    };
    this.approveQuestItemUseAction = {
      name: 'Approve Equipping',
      isApproved: true,
      onClick: () => this.approveQuestItemUse(),
      onHoverMessage: `Explorers can use items to help on quests. You must approve their before you can do this`,
      enabled: true
    };
    this.restoreHealthAction = {
      name: 'Restore Health',
      isApproved: true,
      onClick: () => this.restoreHealth(),
      onHoverMessage: `Restore an explorers health using health items from your barn`,
      enabled: true
    };
    this.aprovePaymentAction = {
      name: 'Approve Corn Use',
      isApproved: true,
      onClick: () => this.aprovePayment(),
      onHoverMessage: 'Explorers need Corn to survive on a quest, you must approve the use of Corn by you Explorers',
      enabled: true
    };
    this.completeQuestAction = {
      name: 'Open Pack',
      isApproved: true,
      onClick: () => this.completeQuest(),
      onHoverMessage: `${
        this.props.character!.name
      } has returned exploring. Open Pack to see what they have discovered.`,
      enabled: true
    };
    this.beginQuestAction = {
      name: 'Begin Quest',
      isApproved: true,
      onClick: () => this.beginQuest(),
      onHoverMessage: `Send ${this.props.character!.name} on a quest to find materials and perhaps new Land`,
      enabled: true
    };
    this.approveHealthItemUseAction = {
      name: 'Approve Healing',
      isApproved: true,
      onClick: () => this.approveHealthItemUse(),
      onHoverMessage: `Explorers can use items for healing. You must approve their before you can do this`,
      enabled: true
    };
  }

  componentDidUpdate(prevPros: IExplorerProps) {
    if (prevPros.refreshedAtBlock !== this.props.refreshedAtBlock) {
      this.updateState(this.props);
    } else return null;
  }

  async updateState(props: IExplorerProps) {
    this.updateItemsReceivedState().then(items => {
      this.updateQuestState(this.props, items);
    });
  }

  async updateItemsReceivedState(): Promise<Partial<IExplorerState>> {
    var items = await this.props.questService!.getCharactersItemsReceived(this.props.character!);
    if (items.length === 0) return {};

    var maxBlockNumber = items.reduce((max, item) => {
      return item.blockNumber > max.blockNumber ? item : max;
    }).blockNumber;

    var recentItems = items
      .filter(item => item.blockNumber === maxBlockNumber)
      .reduce((acc, curr) => {
        var item = this.props.itemService!.itemMap.get(BigNumber.from(curr.itemID).toNumber());
        if (item) {
          if (acc.has(item!.id)) {
            var currAmount = acc.get(item!.id)!.received;
            acc.set(item!.id, {
              item: item!,
              blockNumber: curr!.blockNumber,
              received: BigNumber.from(curr.itemAmount).toNumber() + currAmount
            });
          } else {
            acc.set(item!.id, {
              item: item!,
              blockNumber: curr!.blockNumber,
              received: BigNumber.from(curr.itemAmount).toNumber()
            });
          }
        }
        return acc;
      }, new Map<number, IExplorerItems>());

    var landRecent = items
      .filter(item => item.blockNumber === maxBlockNumber)
      .reduce((acc, curr) => {
        acc = acc + +Utils.formatUnits(curr.landAmount);
        return acc;
      }, 0);

    let history = items.reduce((acc, curr) => {
      var item = this.props.itemService!.itemMap.get(BigNumber.from(curr.itemID).toNumber());
      if (item) {
        if (acc.has(item!.id)) {
          var currAmount = acc.get(item!.id)!.received;
          acc.set(item!.id, {
            item: item!,
            blockNumber: curr!.blockNumber,
            received: BigNumber.from(curr.itemAmount).toNumber() + currAmount
          });
        } else {
          acc.set(item!.id, {
            item: item!,
            blockNumber: curr!.blockNumber,
            received: BigNumber.from(curr.itemAmount).toNumber()
          });
        }
      }
      return acc;
    }, new Map<number, IExplorerItems>());

    let landHistory = items.reduce((acc, curr) => {
      acc = acc + +Utils.formatUnits(curr.landAmount);
      return acc;
    }, 0);

    return {
      recent: [...recentItems.values()],
      landRecent: landRecent,
      history: [...history.values()],
      landHistory: landHistory
    };
  }

  async updateQuestState(props: IExplorerProps, itemsReceivedState: any) {
    var quest = await props.questService?.getQuestState(props.character!);
    if (!quest) return;

    var health = await props.questService!.getCharactersHealth(props.character!);

    const networkBlockTime = props.ethereumService!.networkManager!.network!.blocktime;
    const questCountdownSecs = networkBlockTime * (quest[1] - props.ethereumService!.walletData.stdBlockNumber);

    var explorerState = ExplorerState.Idle;
    if (quest[0] <= 0) {
      explorerState = ExplorerState.New;
    } else if (questCountdownSecs > 0) {
      explorerState = ExplorerState.Exploring;
    } else if (quest[2]) {
      explorerState = ExplorerState.Completed;
    }

    var questState = {
      explorerState: explorerState,
      questCountdownSecs: questCountdownSecs,
      questCountdownTime: Explorer.getTimeRemaining(questCountdownSecs),
      questLength: quest[0],
      questActive: quest[2],
      health: health,
      refreshedAtBlock: props.refreshedAtBlock
    };
    this.setState({ ...questState, ...itemsReceivedState });
  }

  static getTimeRemaining(questCountdownSecs: number): ITime {
    const secondsRemaining = questCountdownSecs < 0 ? 0 : Math.trunc(questCountdownSecs);

    return {
      days: secondsRemaining < 0 ? '00' : ('0' + Math.floor(secondsRemaining / (60 * 60 * 24))).slice(-2),
      hours: secondsRemaining < 0 ? '00' : ('0' + Math.floor((secondsRemaining / (60 * 60)) % 24)).slice(-2),
      mins: secondsRemaining < 0 ? '00' : ('0' + Math.floor((secondsRemaining / 60) % 60)).slice(-2),
      secs: secondsRemaining < 0 ? '00' : ('0' + (secondsRemaining % 60)).slice(-2),
      isComplete: secondsRemaining < 0
    };
  }

  aprovePayment() {
    this.props.questService?.approvePayment();
  }

  approveQuestItemUse() {
    this.props.questService?.approveQuestItemUse();
  }

  approveHealthItemUse() {
    this.props.questService?.approveHealthItemUse();
  }

  releaseExplorer() {
    this.props.questService?.releaseExplorer(this.props.character!);
  }

  beginQuest() {
    this.props.onBeginQuest({
      questService: this.props.questService!,
      itemService: this.props.itemService!,
      character: this.props.character!,
      health: this.state.health
    });
  }

  restoreHealth() {
    this.props.onRestoreHealth({
      questService: this.props.questService!,
      itemService: this.props.itemService!,
      character: this.props.character!,
      health: this.state.health
    });
  }

  completeQuest() {
    this.props.questService?.completeQuest(this.props.character!, []);
  }

  toggleDropdown(e: any) {
    if (this.dropdownBody!.current!.contains(e.relatedTarget)) return;

    this.setState({
      dropdownActiveClass: this.state.dropdownActiveClass === '' ? 'is-active' : ''
    });
  }

  tickerClass() {
    if (+this.state.questCountdownTime.days > 0) {
      return 'days';
    }
    if (+this.state.questCountdownTime.hours > 0) {
      return 'hours';
    }
    if (+this.state.questCountdownTime.mins > 0) {
      return 'mins';
    }
  }

  render() {
    return (
      <div className="explorer is-flex is-flex-direction-column is-justify-content-center">
        {/* <ul>
          <li>state:{this.state.explorerState}</li>
          <li>block:{this.state.refreshedAtBlock}</li>
          <li>charid:{this.props.character!.id}</li>
          <li>countdownSecs:{this.state.questCountdownSecs}</li>
          <li>questLength:{this.state.questLength}</li>
          <li>hasrecent:{this.state.recent ? 'true' : 'false'}</li>
          <li>active:{this.state.questActive ? 'true' : 'false'}</li>
        </ul> */}
        <Character
          actionsLv1={this.getActions1()}
          actionsLv2={this.getActions2()}
          actionsLv3={[]}
          onCardClick={() => {}}
          nft={this.props.character}
          isSample={false}
          health={this.state.health}></Character>
        <div className="quest-details">
          <React.Fragment>
            {/* FIRST QUEST */}
            {this.state.explorerState === ExplorerState.New && (
              <div
                className="quest-items"
                style={{
                  backgroundImage: `linear-gradient(rgba(10, 10, 10, 0.6), rgba(10, 10, 10, 1)),url(${
                    this.props.character!.image
                  })`
                }}>
                <div className="quest-item-title pt-2">Explorer Waiting</div>
                <div className="my-2 pt-2 px-6 is-size-7 has-light-text">
                  Your explorer is waiting to go on their first quest
                </div>
                <div className="first-quest">
                  <div className="quest-action">
                    <button className="button is-transparent-action mb-3" onClick={() => this.beginQuest()}>
                      <span>Begin Quest</span>
                    </button>
                  </div>
                </div>
              </div>
            )}
            {/* EXPLORING */}
            {this.state.explorerState === ExplorerState.Exploring && (
              <div
                className="quest-items"
                style={{
                  backgroundImage: `linear-gradient(rgba(10, 10, 10, 0.6), rgba(10, 10, 10, 1)),url(${
                    this.props.character!.image
                  })`
                }}>
                <div className="quest-item-title pt-2">Exploring</div>
                <div className="my-2 pt-2 is-size-7 has-light-text">Returning In</div>
                <div className="questing">{this.renderCountDown()}</div>
              </div>
            )}
            {/* RETURNED */}
            {this.state.explorerState === ExplorerState.Completed && (
              <div
                className="quest-items"
                style={{
                  backgroundImage: `linear-gradient(rgba(10, 10, 10, 0.6), rgba(10, 10, 10, 1)),url(${
                    this.props.character!.image
                  })`
                }}>
                <div className="quest-item-title pt-2">Explorer Returned</div>
                <div className="my-2 pt-2 is-size-7 has-light-text">Your explorer has discoveries</div>
                <div className="last-quest">{this.renderPack()}</div>
              </div>
            )}
            {/* IDLE */}
            {this.state.explorerState === ExplorerState.Idle && (
              <div
                className="quest-items"
                style={{
                  backgroundImage: `linear-gradient(rgba(10, 10, 10, 0.6), rgba(10, 10, 10, 1)),url(${
                    this.props.character!.image
                  })`
                }}>
                <div className="quest-item-title pt-2">Explorer Idle</div>
                <div className="my-2 pt-2 is-size-7 has-light-text">Your explorer has just discovered</div>
                <div className="last-quest">
                  <React.Fragment>
                    {this.state.recent.map((i, index) => (
                      <div key={index}>{this.renderDiscoveredItem(i)}</div>
                    ))}
                    {this.renderDiscoveredLand(this.state.landRecent)}
                  </React.Fragment>
                </div>
              </div>
            )}
          </React.Fragment>
          <React.Fragment>
            {this.state.history.length > 0 && (
              <div
                className="quest-items mt-5"
                style={{
                  backgroundImage: `linear-gradient(rgba(10, 10, 10, 0.6), rgba(10, 10, 10, 1)),url(${
                    this.props.character!.image
                  })`
                }}>
                <div className="quest-item-title pt-2">All Discoveries</div>
                <div className="quest-history py-2">
                  {this.state.history.map((i, index) => (
                    <div key={index}>{this.renderDiscoveredItem(i)}</div>
                  ))}
                  {this.renderDiscoveredLand(this.state.landHistory)}
                </div>
              </div>
            )}
          </React.Fragment>
        </div>
      </div>
    );
  }

  getActions1(): ICharacterAction[] {
    if (!this.props.questService!.isPaymentApproved) {
      return [this.aprovePaymentAction!];
    } else if (this.state.explorerState === ExplorerState.Completed) {
      return [this.completeQuestAction!];
    } else if (this.state.explorerState === ExplorerState.New || this.state.explorerState === ExplorerState.Idle) {
      return [
        this.beginQuestAction!,
        !this.props.questService!.isHealthItemUseApproved ? this.approveHealthItemUseAction! : this.restoreHealthAction!
      ];
    } else {
      return [this.exploringAction!];
    }
  }

  getActions2(): ICharacterAction[] {
    if (this.state.explorerState === ExplorerState.New || this.state.explorerState === ExplorerState.Idle) {
      return this.props.questService!.isQuestItemUseApproved
        ? [this.releaseExplorerAction!]
        : [this.releaseExplorerAction!, this.approveQuestItemUseAction!];
    } else {
      return [];
    }
  }

  renderDiscoveredItem(ei: IExplorerItems) {
    return (
      <div className="quest-item">
        <img alt={ei.item?.name} className="token" src={ei.item?.imageUrl ?? unknownItem} />
        <span className="item-name">{ei.item?.name}</span>
        <span className="item-balance pl-2">{ei.received}</span>
      </div>
    );
  }

  renderDiscoveredLand(landAmount: number) {
    return (
      <React.Fragment>
        {+Utils.formatUnits(landAmount) > 0 && (
          <div className="quest-item">
            <div className="land-token">
              <img alt="LAND" className="token" src={landIcon} />
            </div>
            <span className="item-name">LAND</span>
            <span className="item-balance pl-2">{Utils.toFixed(landAmount, 0)}</span>
          </div>
        )}
      </React.Fragment>
    );
  }

  renderPack() {
    return (
      <React.Fragment>
        <div className="pack-item">
          <Unrevealed></Unrevealed>
          <span className="filler"></span>
        </div>
        <div className="pack-action">
          <button className="button is-transparent-action mt-2 mb-3" onClick={() => this.completeQuest()}>
            <span>Open Pack</span>
          </button>
        </div>
      </React.Fragment>
    );
  }

  renderCountDown() {
    return (
      <div className="countdown-item quest-countdown">
        <div className="countdown">
          {this.state.questCountdownSecs > 0 && (
            <div className="is-flex is-flex-direction-column">
              <div className="is-flex">
                <div className={'ticker ' + this.tickerClass()}>
                  <div className="digits">
                    <Flip value={this.state.questCountdownTime.days} />
                  </div>
                  <div>
                    <div className="units">days</div>
                  </div>
                </div>
                <div className={'ticker ' + this.tickerClass()}>
                  <div className="digits">
                    <Flip value={this.state.questCountdownTime.hours} />
                  </div>
                  <div>
                    <div className="units">hours</div>
                  </div>
                </div>
                <div className={'ticker ' + this.tickerClass()}>
                  <div className="digits">
                    <Flip value={this.state.questCountdownTime.mins} />
                  </div>
                  <div>
                    <div className="units">mins</div>
                  </div>
                </div>
              </div>
            </div>
          )}
        </div>
      </div>
    );
  }
}
