import { BigNumber } from '@ethersproject/bignumber';
import unrevealed from '../assets/unrevealed.png';
import { Utils } from '../services/Utils';

export enum CharacterStat {
  Stamina = 1,
  Strength = 2,
  Speed = 3,
  Courage = 4,
  Intelligence = 5
}

export interface ICharacterAttribute {
  id: number;
  display_type: string;
  trait_type: string;
  badge_type: string;
  value: any;
}

export interface ICharacterStats {
  name: string;
  value: number;
}

export interface ICharacterDetails {
  symbol: string;
  index: number;
  id: number;
  attributes: ICharacterAttribute[];
  stats: ICharacterStats[];
  description: string;
  image: string;
  revealed: boolean;
  name: string;
  boostsRemaining: number;
}

export interface ICharacterHealth {
  max: number;
  remaining: number;
}

export function getHealthPercent(health: ICharacterHealth) {
  if (!health) return 0;
  return (health!.remaining / health!.max) * 100;
}

export function isHealthFull(health: ICharacterHealth): boolean {
  if (!health) return false;
  return health.remaining === health.max;
}

export async function loadCharacterDetailsFromUri(
  index: number,
  tokenId: BigNumber,
  tokenUri: string,
  stats: any,
  boostsRemaining: number
): Promise<ICharacterDetails> {
  const meta = tokenUri.replace('https://farmland-game.github.io/assets/', './assets/');
  const revealed = Utils.fileExists(meta);

  var nft: ICharacterDetails;
  if (revealed) {
    const tokenResponse = await fetch(meta);
    nft = (await tokenResponse.json()) as ICharacterDetails;
    nft.image = nft.image.replace('https://farmland-game.github.io/assets/', './assets/');
    nft.revealed = true;
    nft.index = index;
    nft.id = tokenId.toNumber();
    nft.stats = [];
    nft.boostsRemaining = boostsRemaining;
    nft.stats.push({ name: 'Stamina', value: stats['trait' + CharacterStat.Stamina].toNumber() });
    nft.stats.push({ name: 'Strength', value: stats['trait' + CharacterStat.Strength].toNumber() });
    nft.stats.push({ name: 'Speed', value: stats['trait' + CharacterStat.Speed].toNumber() });
    nft.stats.push({ name: 'Courage', value: stats['trait' + CharacterStat.Courage].toNumber() });
    nft.stats.push({ name: 'Intelligence', value: stats['trait' + CharacterStat.Intelligence].toNumber() });
    var maxAttId = Math.max.apply(
      Math,
      nft.attributes.map(a => {
        return a.id;
      })
    );

    if (stats['trait1'].toNumber() > 95) {
      nft.attributes.push({
        id: ++maxAttId,
        display_type: 'Stat',
        trait_type: 'Stat Badge',
        badge_type: 'Stat',
        value: 'Tank'
      });
    }

    if (stats['trait2'].toNumber() > 95) {
      nft.attributes.push({
        id: ++maxAttId,
        display_type: 'Stat',
        trait_type: 'Stat Badge',
        badge_type: 'Stat',
        value: 'Warrior'
      });
    }

    if (stats['trait3'].toNumber() > 95) {
      nft.attributes.push({
        id: ++maxAttId,
        display_type: 'Stat',
        trait_type: 'Stat Badge',
        badge_type: 'Stat',
        value: 'Lightning'
      });
    }

    if (stats['trait4'].toNumber() > 95) {
      nft.attributes.push({
        id: ++maxAttId,
        display_type: 'Stat',
        trait_type: 'Stat Badge',
        badge_type: 'Stat',
        value: 'Hero'
      });
    }

    if (stats['trait5'].toNumber() > 95) {
      nft.attributes.push({
        id: ++maxAttId,
        display_type: 'Stat',
        trait_type: 'Stat Badge',
        badge_type: 'Stat',
        value: 'Genius'
      });
    }

    var badgesAttribute = nft.attributes.find(a => a.trait_type === 'Badges');

    if (badgesAttribute) {
      var badges = badgesAttribute?.value.split(',');

      badges.forEach((b: string) => {
        if (b !== 'None') {
          nft.attributes.push({
            id: ++maxAttId,
            display_type: 'Badge',
            trait_type: 'Badge',
            badge_type: b === 'Founder' || b === 'Unique' ? 'Special' : 'Image',
            value: b
          });
        }
      });
    }

    nft.attributes = nft.attributes.sort((a: ICharacterAttribute, b: ICharacterAttribute) => {
      return a.badge_type?.length < b.badge_type?.length ? 1 : a.badge_type?.length > b.badge_type?.length ? -1 : 0;
    });
  } else {
    nft = newUnrevealed(index);
  }

  return nft;
}

export function newSampleCharacterDetails(
  name: string,
  img: any,
  attributes: ICharacterAttribute[],
  index: number
): ICharacterDetails {
  return {
    symbol: 'CHARACTER',
    index: index,
    id: index,
    attributes: attributes,
    stats: [
      {
        name: 'Strength',
        value: Math.floor(Math.random() * 70 + 20)
      },
      {
        name: 'Speed',
        value: attributes.some(a => a.value === 'Lightning') ? 97 : Math.floor(Math.random() * 70 + 20)
      },
      { name: 'Stamina', value: Math.floor(Math.random() * 70 + 20) },
      { name: 'Intelligence', value: Math.floor(Math.random() * 70 + 20) },
      { name: 'Courage', value: Math.floor(Math.random() * 70 + 20) }
    ],
    description:
      attributes.length === 0
        ? 'A common Farmland Character'
        : attributes.length === 1
        ? 'An uncommon Farmland Character'
        : 'A rare Farmland Character',
    image: img,
    revealed: true,
    name: name,
    boostsRemaining: 10
  };
}

export function newUnrevealed(index: number): ICharacterDetails {
  return {
    symbol: 'CHARACTER',
    index: index,
    id: index,
    attributes: [{ id: 0, display_type: '', trait_type: 'Unknown', badge_type: 'Unknown', value: 'Unknown' }],
    stats: [
      { name: 'Strength', value: 0 },
      { name: 'Speed', value: 0 },
      { name: 'Stamina', value: 0 },
      { name: 'Intelligence', value: 0 },
      { name: 'Courage', value: 0 }
    ],
    description: 'An unknown Farmland Character',
    image: unrevealed,
    revealed: false,
    name: '??? ???',
    boostsRemaining: 10
  };
}
