import { BigNumber, constants, ethers } from 'ethers';
import { CharacterStat, ICharacterDetails, loadCharacterDetailsFromUri } from '../models/CharacterDetails';
import { EthereumService } from './EthereumService';
import { MessagingService } from './MessagingService';
import { Utils } from './Utils';
import { INonFungibleAssetService } from './Interfaces';
import { ICharacterConfig, IEmploymentConfig } from '../models/CharacterConfig';
import { CharacterEmploymentServiceFactory, ICharacterEmploymentService } from './CharacterEmploymentService';

export class CharacterService implements INonFungibleAssetService {
  walletAddress?: string;
  contract?: ethers.Contract;
  boostPaymentContract?: ethers.Contract;
  count: number = 0;
  gasPrice?: string | number;
  tokenUri?: string;
  isBoostApproved: boolean = false;
  maxStatBoosts: number = 10;
  mintPrice?: BigNumber;
  boostPrice?: BigNumber;
  boostPaymentBalance?: number;
  nfts: ICharacterDetails[] = [];

  employmentServices?: ICharacterEmploymentService[];

  actionText: string = 'Mint';
  actionUrl: string = '/characters';

  constructor(
    public config: ICharacterConfig,
    private messagingService: MessagingService,
    private ethereumService: EthereumService
  ) {
    this.walletAddress = this.ethereumService!.walletData.walletAddress;

    this.contract = new ethers.Contract(
      this.config.contractAddress,
      this.config.contractContants.contractAbi,
      ethereumService.signer
    );

    this.boostPaymentContract = new ethers.Contract(
      this.config.boostPayConfig.contractAddress,
      this.config.boostPayConfig.assetConstants!.contractAbi,
      this.ethereumService.signer!
    );

    this.employmentServices = [...this.config.employmentConfigs].map((ec: IEmploymentConfig) => {
      return CharacterEmploymentServiceFactory.create(this.config, ec, this.messagingService, this.ethereumService);
    });

    this.loadStatics();
    this.setupEventHandlers();
  }

  async loadStatics() {
    this.mintPrice = await this.contract!.price();
    this.maxStatBoosts = await this.contract!.maxTraitBoosts();
    this.boostPrice = await this.contract!.traitBoostPrice();
    this.boostPaymentBalance = +Utils.formatUnits(await this.boostPaymentContract!.balanceOf(this.walletAddress));
  }

  async loadOwned() {
    const tokenCount = await this.contract!.balanceOf(this.walletAddress);

    var nftPromises = [...Array(tokenCount.toNumber()).keys()].map(index => {
      return this.loadDetails(index);
    });

    var characters = await Promise.all(nftPromises);
    this.nfts = characters.sort((a, b) => (a.name < b.name ? -1 : 1));

    if (this.boostPaymentContract) {
      const boostAllowance = await this.boostPaymentContract!.allowance(
        this.walletAddress,
        this.config.contractAddress
      );
      this.isBoostApproved = boostAllowance._hex !== constants.Zero._hex;
    }
  }

  async loadDetails(index: number): Promise<ICharacterDetails> {
    const tokenId = await this.contract!.tokenOfOwnerByIndex(this.walletAddress, BigNumber.from(index));
    const tokenUri = await this.contract!.tokenURI(tokenId.toNumber());
    const stats = await this.contract!.collectibleTraits(tokenId);
    const boostsRemaining =
      this.maxStatBoosts - (await this.contract!.collectibleTraitBoostTracker(tokenId.toNumber()));

    return await loadCharacterDetailsFromUri(index, tokenId, tokenUri, stats, boostsRemaining);
  }

  mint(amount: number) {
    let overrides = {
      value: this.mintPrice!.mul(BigNumber.from(amount))
    };
    return this.contract!.mintCollectible(BigNumber.from(amount), overrides).then(
      () =>
        this.messagingService.events.next({
          message: `Minting of Character(s) is pending`,
          pending: true
        }),
      (error: any) =>
        this.messagingService.errors.next({
          message: `Error minting Characters : (${Utils.getContractErrorMessage(error)})`,
          error: error
        })
    );
  }

  approveBoost(): Promise<any> {
    var amount = Utils.parseUnits('1000000000');

    return this.boostPaymentContract!.approve(this.config.contractAddress, amount).then(
      () =>
        this.messagingService.events.next({
          message: `Approval of Character stat boost is pending`,
          pending: true
        }),
      (error: any) =>
        this.messagingService.errors.next({
          message: `Error approving Character stat boost: (${Utils.getContractErrorMessage(error)})`,
          error: error
        })
    );
  }

  async boost(characterDetails: ICharacterDetails, stat: CharacterStat, boost: number) {
    const tokenId = await this.contract!.tokenOfOwnerByIndex(
      this.walletAddress,
      BigNumber.from(characterDetails.index)
    );

    return this.contract!.boostTrait(tokenId, BigNumber.from(stat), BigNumber.from(boost)).then(
      () =>
        this.messagingService.events.next({
          message: `Boosting of Character is pending`,
          pending: true
        }),
      (error: any) =>
        this.messagingService.errors.next({
          message: `Error boosting Character : (${Utils.getContractErrorMessage(error)})`,
          error: error
        })
    );
  }

  removeEventHandlers() {
    if (this.contract) {
      this.contract!.removeAllListeners();
    }
  }

  setupEventHandlers() {
    this.removeEventHandlers();

    var update = (message: string) =>
      this.loadOwned().then(() => {
        this.messagingService.events.next({ message: message });
      });

    // Commented out because approvals emitted on ticket buy

    // if (this.boostPaymentContract) {
    //   this.boostPaymentContract!!.on(
    //     {
    //       address: this.ethereumService.walletData.walletAddress,
    //       topics: [ethers.utils.id('Approval(address,address,uint256)')]
    //     },
    //     (owner: any, spender: any, amount: any) => {
    //       if (owner === this.ethereumService.walletData.walletAddress) {
    //         update(`${this.config.assetConstants?.symbol} stat boost approved`);
    //       }
    //     }
    //   );
    // }

    if (this.contract) {
      this.contract!.on(
        {
          address: this.ethereumService.walletData.walletAddress,
          topics: [ethers.utils.id('ApprovalForAll(address,address,bool)')]
        },
        (owner, operator, approved) => {
          if (owner === this.ethereumService.walletData.walletAddress) {
            if (approved) {
              update(`Characters have been approved`);
            } else {
              this.messagingService.errors.next({
                message: `Characters were not approved`,
                error: `Characters were not approved`
              });
            }
          }
        }
      );

      this.contract!.on(
        {
          address: this.ethereumService.walletData.walletAddress,
          topics: [ethers.utils.id('Transfer(address,address,uint256)')]
        },
        (from, to, tokenId) => {
          if (to === this.ethereumService.walletData.walletAddress) {
            update(`Character available`);
          }
        }
      );

      this.contract!.on(
        {
          address: this.ethereumService.walletData.walletAddress,
          topics: [ethers.utils.id('CharacterBoosted(address,uint256,uint256,uint256,uint256)')]
        },
        (booster, id, trait, boost, pricePaid) => {
          if (booster === this.ethereumService.walletData.walletAddress) {
            update(`Character stat boosted`);
          }
        }
      );
    }
  }
}
