import React, { Component } from 'react';
import { getHealthPercent, ICharacterDetails, ICharacterHealth, isHealthFull } from '../../models/CharacterDetails';
import { ICharacterAction } from '../../models/ICharacterAction';
import { Unrevealed } from './Unrevealed';

export interface ICharacterProps {
  nft?: ICharacterDetails;
  actionsLv1: ICharacterAction[];
  actionsLv2: ICharacterAction[];
  actionsLv3: ICharacterAction[];
  onCardClick?: any;
  isSample: boolean;
  health?: ICharacterHealth;
}

export class Character extends Component<ICharacterProps> {
  constructor(props: any) {
    super(props);
    this.click = this.click.bind(this);
  }

  click() {
    this.props.onCardClick();
  }

  render() {
    return (
      <div className="stage mr-3 mb-5 is-flex is-flex-direction-column is-align-items-center noselect">
        <div
          onClick={this.click}
          className={`card p-3 ${this.rarityClass()}`}
          style={{
            backgroundImage: `linear-gradient(rgba(10, 10, 10, 0.6), rgba(10, 10, 10, 1)),url(${this.props.nft!.image})`
          }}>
          <div className="card-image">
            <figure className="image is-3by3">
              {!this.props.nft?.revealed && <Unrevealed></Unrevealed>}
              {this.props.nft?.revealed && (
                <img className="nft" src={this.props.nft!.image} alt="Farmland Collectible" />
              )}
              {this.props.isSample && <h3 className="nft-sample">Sample</h3>}
            </figure>
          </div>
          <div className="card-content p-0 py-2">
            <div className="content">
              <div className="is-flex is-justify-content-space-between is-align-items-center">
                <span>
                  <p className="character-title is-4 mb-0 mt-1">{this.props.nft!.name}</p>
                  <p className="is-size-6 mb-0">{this.description()}</p>
                </span>
                <span className="is-flex is-justify-content-end is-align-items-end character-boost-remaining">
                  <span className="fa-fw">
                    <i className="fas fa-bolt fa-inverse"></i>
                  </span>
                  {this.props.nft!.boostsRemaining}
                </span>
              </div>
              <div className="attributes is-flex is-flex-direction-row is-align-items-self-end is-flex-wrap-wrap">
                {this.renderAttributes()}
              </div>
              <div className="stats is-flex is-flex-direction-column is-align-items-end has-text-weight-bold mt-2">
                {this.props.nft!.stats.map(s => {
                  return (
                    <div key={s.name} className="is-flex is-justify-content-space-between">
                      <p className="is-size-7 mb-0">{s.name}</p>
                      <p className={`${this.statClass(s.value)} is-size-7 mb-0`}>{s.value > 0 ? s.value : '??'}</p>
                    </div>
                  );
                })}
              </div>
              <div className="health">{this.renderHealth()}</div>
            </div>
          </div>
        </div>
        <div
          className={`${
            this.props.actionsLv3.length > 0
              ? 'lv3-stage-action stage-action'
              : this.props.actionsLv2.length > 0
              ? 'lv2-stage-action stage-action'
              : 'lv1-stage-action stage-action'
          }`}>
          <div className="is-flex is-flex-direction-column" style={{ zIndex: 100 }}>
            <div className="is-flex is-flex-direction-row" style={{ zIndex: 100 }}>
              {this.props.actionsLv1!.map((a: ICharacterAction) => {
                return (
                  <button
                    key={a.name}
                    className={`${a.onHoverMessage ? 'tooltip' : ''} button is-transparent-action activate-button`}
                    onClick={() => a.onClick()}
                    disabled={!a.enabled}>
                    {!a.isApproved && <span>Approve {a.name}</span>}
                    {!!a.isApproved && <span>{a.name}</span>}
                    <span className="tooltip-text tooltip-text-up">{a.onHoverMessage}</span>
                  </button>
                );
              })}
            </div>
            <div className="is-flex is-flex-direction-row">
              {this.props.actionsLv2.length > 0 && (
                <React.Fragment>
                  {this.props.actionsLv2.map((a: ICharacterAction) => {
                    return (
                      <button
                        key={a.name}
                        className={`${a.onHoverMessage ? 'tooltip' : ''} button is-transparent-action activate-button`}
                        onClick={() => a.onClick()}
                        disabled={!a.enabled}>
                        <span>{a.name}</span>
                        <span className="tooltip-text">{a.onHoverMessage}</span>
                      </button>
                    );
                  })}
                </React.Fragment>
              )}
            </div>
            <div className="is-flex is-flex-direction-row">
              {this.props.actionsLv3.length > 0 && (
                <React.Fragment>
                  {this.props.actionsLv3.map((a: ICharacterAction) => {
                    return (
                      <button
                        key={a.name}
                        className={`${a.onHoverMessage ? 'tooltip' : ''} button is-transparent-action activate-button`}
                        onClick={() => a.onClick()}
                        disabled={!a.enabled}>
                        <span>{a.name}</span>
                        <span className="tooltip-text">{a.onHoverMessage}</span>
                      </button>
                    );
                  })}
                </React.Fragment>
              )}
            </div>
          </div>
        </div>
      </div>
    );
  }

  description(): string {
    if (!this.props.nft!.revealed) {
      return 'An unknown Farmland character';
    }
    if (this.props.nft!.attributes.filter(a => a.badge_type === 'Special' && a.value === 'Unique').length > 0)
      return 'A unique Farmland character';
    var specialAttributes = this.props.nft!.attributes.filter(
      a => ['Special', 'Image', 'Stat'].indexOf(a.badge_type) >= 0
    ).length;
    if (specialAttributes > 3) return 'A legendary Farmland character';
    if (specialAttributes > 2) return 'An epic Farmland character';
    if (specialAttributes > 1) return 'A rare Farmland character';
    if (specialAttributes > 0) return 'An uncommon Farmland character';
    return 'A common Farmland character';
  }

  rarityClass(): string {
    if (!this.props.nft!.revealed) {
      return 'rarity unknown';
    }
    if (this.props.nft!.attributes.filter(a => a.badge_type === 'Special' && a.value === 'Unique').length > 0)
      return 'rarity unique';
    var specialAttributes = this.props.nft!.attributes.filter(
      a => ['Special', 'Image', 'Stat'].indexOf(a.badge_type) >= 0
    ).length;
    if (specialAttributes > 3) return 'rarity legendary';
    if (specialAttributes > 2) return 'rarity epic';
    if (specialAttributes > 1) return 'rarity rare';
    if (specialAttributes > 0) return 'rarity uncommon';
    return '';
  }

  statClass(value: number): string {
    if (value > 95) return 'super-stat';
    if (value > 79) return 'good-stat';
    return '';
  }

  renderAttributes() {
    return (
      <React.Fragment>
        {this.props
          .nft!.attributes.filter(a => ['Unknown', 'Special', 'Image', 'Stat'].indexOf(a.badge_type) >= 0)
          .map(a => {
            return (
              <span key={a.id} className={`tag is-primary ${a.badge_type}`}>
                {a.value}
              </span>
            );
          })}
      </React.Fragment>
    );
  }

  renderHealth() {
    return (
      <React.Fragment>
        {this.props.health && (
          <div className="health-container">
            <div
              className={`health-bar ${isHealthFull(this.props.health) ? 'health-full' : ''}`}
              style={{
                background: `linear-gradient(to right, var(--red) 0%, var(--green) ${
                  65 + (100 - getHealthPercent(this.props.health))
                }%)`,
                width: `${getHealthPercent(this.props.health)}%`
              }}>
              Health
            </div>
          </div>
        )}
      </React.Fragment>
    );
  }
}
