import React, { Component } from 'react';
// @ts-ignore
import { NavLink } from 'react-router-dom';
import { Subscription } from 'rxjs';
import { EthereumService } from '../../services/EthereumService';
import { MessagingService } from '../../services/MessagingService';
import { IQuestService, QuestServiceFactory } from '../../services/QuestService';
import { ICharacterDetails, ICharacterHealth } from '../../models/CharacterDetails';
import { ItemService } from '../../services/ItemService';
import { Quest } from './Quest';
import { BeginQuest } from './BeginQuest';
import { Utils } from '../../services/Utils';
import { RestoreHealth } from './RestoreHealth';

import '../Content.scss';
import '../Tables.scss';
import './Quests.scss';
import { Mutex } from 'async-mutex';

enum QuestsToDisplay {
  All,
  Employed
}

interface IQuestsProps {
  messagingService: MessagingService;
  ethereumService: EthereumService;
}

interface IQuestsState {
  itemService: ItemService | null;
  questsDisplayed: QuestsToDisplay;
  beginQuest: IQuestCharacter | null;
  restoreHealth: IQuestCharacter | null;
  refreshedAtBlock: number;
}

interface IQuestCharacters {
  questService: IQuestService;
  characters: ICharacterDetails[];
  refreshedAtBlock: number;
}

export interface IQuestCharacter {
  questService: IQuestService;
  itemService: ItemService;
  character: ICharacterDetails;
  health: ICharacterHealth;
}

export class Quests extends Component<IQuestsProps, IQuestsState> {
  subscription: Subscription;
  walletAddress?: string;
  chainId?: number;
  questServices: IQuestService[] | undefined;
  questCharacters: IQuestCharacters[] = [];
  questPaymentName?: string;
  mutex = new Mutex();

  constructor(props: IQuestsProps) {
    super(props);

    this.state = {
      itemService: null,
      questsDisplayed: QuestsToDisplay.All,
      beginQuest: null,
      restoreHealth: null,
      refreshedAtBlock: 0
    };

    this.subscription = new Subscription();

    this.toggleAssetsDisplayed = this.toggleAssetsDisplayed.bind(this);
    this.aproveQuestPayment = this.aproveQuestPayment.bind(this);
    this.beginQuest = this.beginQuest.bind(this);
    this.restoreHealth = this.restoreHealth.bind(this);
  }

  componentDidMount() {
    var eventSubscription = this.props.messagingService.events.subscribe(event => {
      this.refresh();
    });
    this.subscription.add(eventSubscription);
    this.refresh();
  }

  componentWillUnmount() {
    this.subscription.unsubscribe();
  }

  toggleAssetsDisplayed() {
    this.setState({
      questsDisplayed:
        this.state.questsDisplayed === QuestsToDisplay.Employed ? QuestsToDisplay.All : QuestsToDisplay.Employed
    });
  }

  getPaymentName(): string {
    if (this.questPaymentName) return this.questPaymentName!;

    if (!this.questCharacters[0]) return '';

    this.questPaymentName = Utils.toTitleCase(
      this.questCharacters[0].questService.questConfig.questPayConfig.assetConstants!.symbol
    );

    return this.questPaymentName!;
  }

  aproveQuestPayment() {
    if (!this.questCharacters[0]) return;

    this.questCharacters[0].questService!.approvePayment();
  }

  beginQuest(questCharacter: IQuestCharacter) {
    this.setState({ beginQuest: questCharacter, restoreHealth: null });
  }

  restoreHealth(questCharacter: IQuestCharacter) {
    this.setState({ beginQuest: null, restoreHealth: questCharacter });
  }

  async refresh(): Promise<any> {
    if (
      this.props.ethereumService.walletData.walletAddress &&
      this.props.ethereumService.networkManager?.network?.chainId
    ) {
      if (
        this.props.ethereumService.walletData.walletAddress !== this.walletAddress ||
        this.props.ethereumService.networkManager!.network?.chainId !== this.chainId
      ) {
        this.walletAddress = this.props.ethereumService.walletData.walletAddress;
        this.chainId = this.props.ethereumService.networkManager!.network!.chainId;
      }

      if (this.props.ethereumService.networkManager!.network.questConfigs.length > 0) {
        await this.mutex.runExclusive(async () => {
          await this.refreshItems();
          await this.refreshQuests();
          this.setState({ refreshedAtBlock: this.props.ethereumService.walletData.blockNumber });
        });
      }
    }
  }

  async refreshItems(): Promise<any> {
    if (!this.props.ethereumService.networkManager!.network!.itemConfig) {
      return;
    }

    var itemService = this.state.itemService;
    if (!itemService) {
      itemService = new ItemService(
        this.props.ethereumService.networkManager!.network!.itemConfig!,
        this.props.messagingService,
        this.props.ethereumService
      );
    }

    itemService.loadOwned();
    this.setState({ itemService: itemService });
  }

  async refreshQuests(): Promise<any> {
    if (!this.questServices) {
      this.questServices = QuestServiceFactory.createQuests(this.props.messagingService, this.props.ethereumService);
    }

    this.questServices.forEach(async (qs: IQuestService) => {
      await qs.update();
    });

    this.questCharacters = [];
    for (const questService of this.questServices) {
      var characters = await questService.getCharacters();
      this.questCharacters.push({
        questService: questService,
        characters: characters,
        refreshedAtBlock: this.props.ethereumService.walletData.blockNumber
      });
    }
  }

  render() {
    return (
      <section id="content" className="quests is-vcentered">
        <div>
          {!this.props.ethereumService.walletData.isConnected && (
            <span
              className="column meme is-half has-text-centered mt-4"
              style={{ cursor: 'pointer' }}
              onClick={() => this.props.ethereumService.connectWallet()}>
              <h3 className="sub-title py-3">Connect Wallet</h3>
            </span>
          )}
          {this.props.ethereumService.walletData.isConnected && (
            <React.Fragment>
              {!this.state.itemService && (
                <span className="column meme is-half has-text-centered mt-4">
                  <h3 className="sub-title py-3">Coming Soon!</h3>
                </span>
              )}
              {this.questCharacters.length > 0 && !this.questCharacters[0].questService.isPaymentApproved && (
                <span className="column meme is-half has-text-centered mt-4 mb-5">
                  <h3 className="is-body py-3">
                    You must give your characters permission to use your {this.getPaymentName()} before then can go on
                    quests.
                  </h3>
                  <button className="button is-link is-action my-3" onClick={this.aproveQuestPayment}>
                    <span>Approve {this.getPaymentName()} Use</span>
                  </button>
                </span>
              )}
              {this.state.itemService && (
                <React.Fragment>
                  {this.state.questsDisplayed === QuestsToDisplay.All && (
                    <React.Fragment>
                      {this.questCharacters.length > 0 &&
                        this.questCharacters.map(cqs => (
                          <div key={cqs.questService.questConfig.contractAddress}>
                            {cqs.characters.length > 0 && (
                              <div className="tile is-ancestor mx-3">
                                <div className="tile is-vertical is-12 mb-6">
                                  <Quest
                                    ethereumService={this.props.ethereumService}
                                    itemService={this.state.itemService!}
                                    questService={cqs.questService}
                                    characters={cqs.characters}
                                    refreshedAtBlock={cqs.refreshedAtBlock}
                                    onBeginQuest={this.beginQuest}
                                    onRestoreHealth={this.restoreHealth}></Quest>
                                </div>
                              </div>
                            )}
                            {cqs.characters.length === 0 && (
                              <span className="column meme is-half has-text-centered mt-4 mb-5">
                                <h3 className="is-body py-3">You have no characters employed as explorers.</h3>
                                <NavLink
                                  className="button is-link is-action my-3"
                                  to="/characters"
                                  activeStyle={{ color: 'var(--orange2)' }}>
                                  Employ Explorers
                                </NavLink>
                              </span>
                            )}
                          </div>
                        ))}
                      {this.questCharacters.length === 0 && (
                        <span className="column meme is-half has-text-centered mt-4">
                          <h3 className="sub-title py-3">Loading Quests</h3>
                        </span>
                      )}
                    </React.Fragment>
                  )}
                </React.Fragment>
              )}
            </React.Fragment>
          )}
        </div>

        {this.state.beginQuest && (
          <BeginQuest
            ethereumService={this.props.ethereumService}
            questService={this.state.beginQuest.questService!}
            itemService={this.state.beginQuest.itemService!}
            character={this.state.beginQuest.character!}
            close={() => this.setState({ beginQuest: null })}></BeginQuest>
        )}

        {this.state.restoreHealth && (
          <RestoreHealth
            questService={this.state.restoreHealth.questService!}
            itemService={this.state.restoreHealth.itemService!}
            character={this.state.restoreHealth.character!}
            health={this.state.restoreHealth.health}
            close={() => this.setState({ restoreHealth: null })}></RestoreHealth>
        )}
      </section>
    );
  }
}
