import { Subject } from 'rxjs';
import { toast } from 'bulma-toast';

export class MessagingService {
  networkEvents = new Subject();
  events = new Subject();
  errors = new Subject();
  block: number = 0;
  messages: string[] = [];

  constructor() {
    this.networkEvents.subscribe({
      next: (e: any) => this.toast(e)
    });

    this.events.subscribe({
      next: (e: any) => this.toast(e)
    });

    this.errors.subscribe({
      next: (e: any) => this.toast(e)
    });
  }

  toast(e: any) {
    if (e?.error) {
      console.log(e.error);
    }
    if (e?.block > this.block) {
      this.messages = [];
      this.block = e.block;
    }
    if (e?.message) {
      if (this.messages.includes(e.message)) return;
      this.messages.push(e.message);

      toast({
        message: e.message,
        type: e.error ? 'is-danger' : e.pending ? 'is-warning' : 'is-success',
        duration: 5000,
        position: 'bottom-center',
        closeOnClick: true,
        pauseOnHover: true,
        opacity: 0.8,
        dismissible: true
      });
    }
  }
}
